.partner {
  margin-top: 35px;
  text-align: center;
  h3 {
    font-size: 20px;
    color: $font-dark;
  }
  .partners {
    margin-top: 45px;
    display: table;
    border-collapse: collapse;
    width: 100%;
    table-layout: fixed;
    > li {
      display: table-cell;
      @include size(100%, 120px); 
      text-align: left;
      &:last-child {
        width: 190px;
      }
      a {
        display: inline-block;
        border: 1px solid $border-color;
        @include size(190px, 120px);
        text-align: center;
        img {
          @include size(100%);
        }
      }
    }
    
  }
}
.qrskin{
  .ui-dialog-title {
    display: none;
  }
}