.service {
  @include size(100%, 335px);
  padding: 106px 0 120px 0;
  border-bottom: 1px solid $border-color;
  .car-wrap {
    height: 100%;
  }
  ul {
    display: table;
    border-collapse: collapse;
    table-layout: fixed;
    @include size(100%);
    li {
      display: table-cell;
      width: 100%;
      position: relative;
      text-align: center;
      &:before {
        content: '';
        @include absolute(top 0 left 50%);
        margin-left: -38px;
        @include size(76px);
        background: #fff no-repeat center center ;
      }
      span {
        display: inline-block;
        margin-top: 95px;
      }
      &:nth-child(1) {
        &:before {
          background-image: url('../img/z.png');
        }
      }  
      &:nth-child(2) {
        &:before {
          background-image: url('../img/d.png');
        }
      }
      &:nth-child(3) {
        &:before {
          background-image: url('../img/k.png');
        }
      }   
      &:nth-child(4) {
        &:before {
          background-image: url('../img/a.png');
        }
      }
    }
  }
}