@charset 'utf-8';
.main {
    margin: 0 auto;
    input.input, select.select {
        margin: 0 5px 0 0;
        padding: 5px;
        height: 22px;
        line-height: 22px;
        border: 1px solid #cacaca;
        color: #343434;
    } 
    table {
        border-collapse: collapse;
    }
    input.input:focus, textarea.textarea:focus {
        border-color: #ff9100;
        outline: none;
    }
    input.date:focus {
        border-color: #cacaca;
        outline: invert none medium;
    }
    input.datem:focus {
        border-color: #cacaca;
        outline: invert none medium;
    }
    input.error {
        border-color: #d9544f;
    }
    input.success {
        border-color: #16a085;
    }
    .td.normal {
        width: 20%;
    }
    .td.short {
        width: 100px;
    }
    .td.shortest {
        width: 70px;
    }
    .td.medium {
        width: 320px;
    }
    .td.large {
        width: 480px;
    }
    .td.td33 {
        width: 33.3333%;
    }
    
    .wrap {
        margin: 0px auto;
        padding: 30px 0 0 0;
        display: table;
        table-layout: fixed;
        @include size(1250px, 100%);
        .sidebar {
            display: table-cell;
            width: 250px;
            background-color: #fff;
            border-radius: 4px;
            vertical-align: top;
            .menu {
                margin: 0 0 50px 0;
                border-top: 12px solid #4b4b4b;
                border-radius: 4px;
                li {
                    &.on {
                        h3 {
                            background-color: #fff;
                            color: #ff9100;
                            .ico {
                                &.i1 {
                                    background-position: -40px -260px;
                                }
                                &.i2 {
                                    background-position: -40px -320px;
                                }
                                &.i3 {
                                    background-position: -40px -280px;
                                }
                                &.i4 {
                                    background-position: -40px -300px;
                                }
                            }
                        }
                    }
                    h3 {
                        padding: 0 0 0 4px;
                        font-weight: 500;
                        line-height: 44px;
                        .ico {
                            float: left;
                            margin: 15px 12px 0 15px;
                            width: 16px;
                            height: 16px;
                            background: url(../img/icons.png) no-repeat -0px -260px;
                            &.i1 {
                                background-position: -20px -260px;
                            }
                            &.i2 {
                                background-position: -20px -320px;
                            }
                            &.i3 {
                                background-position: -20px -278px;
                            }
                            &.i4 {
                                background-position: -20px -298px;
                            }
                        }
                    }
                    dl {
                      dd {
                        font-size: 13px;
                        line-height: 36px;
                        a {
                            display: block;
                            padding: 0 0 0 38px;
                            color: #343434;
                            .arrow {
                                display: inline-block;
                                width: 16px;
                                height: 10px;
                                background: url(../img/icons.png) no-repeat 0 -460px;
                            }
                        }
                        &.on a {
                            color: #fff;
                            background-color: #ff9100;
                            .arrow {
                                background: url(../img/icons.png) no-repeat 0 -435px;
                            }
                        }
                      }
                    }
                }
            }
        }
        .maincontent {
            display: table-cell;
            width: 1000px;
            input.input,select.select {
                height: 18px;
                line-height: 18px;
            }
            .inner {
                padding: 0 0 25px 0;
                width: 100%;
                background: #f2f4f5;
                .panel {
                    margin: 0px 0px 16px 16px;
                    padding: 0 0 40px 0;
                    background-color: #fff;
                    color: #343434;
                    border-radius: 4px;
                    box-shadow: 0 0 10px #eee;
                    &.datapanel {
                        padding: 0;
                        .head {
                            h3 {
                                font-weight: bold;
                                em {
                                    font-style: normal;
                                    color: rgb(217, 84, 79);
                                    &.gold{
                                        color: #a38f67;
                                    }
                                }
                            }
                        }
                        .content {
                            padding: 0px 20px 25px;
                            &.listtable {
                                padding-top: 10px;
                               .func {
                                    margin: 5px 0;
                                    padding: 0;
                                    font-size: 12px;
                                }
                                .table {
                                    border-spacing: 0;
                                }
                                .tr {
                                    .td {
                                        padding: 8px 10px;
                                        border-bottom: 1px solid #E3E6EB;
                                        line-height: 20px;
                                        color: #999;
                                    }
                                    &.thead {
                                        .td {
                                            border: 1px solid #CBCFD2;
                                            border-left: none;
                                            line-height: 16px;
                                            background-color: #fafafa;
                                            color: #4a4a4a;
                                            &.last {
                                                border-right: none;
                                            }
                                        }
                                    }
                                }
                                
                                
                            }
                        }
                    }
                    .head {
                        height: 14px;
                        font-size: 13px;
                        padding: 25px 20px 16px;
                        border-bottom: 1px solid #f2f2f2;
                        margin-bottom: 15px;
                        h3 {
                            float: left;
                        }
                        .more {
                            float: right;
                        }
                    }
                    .table {
                        display: table;
                        width: 100%;
                        .tr {
                            display: table-row;
                            .td {
                                display: table-cell;
                                vertical-align: middle;
                                padding: 0px;
                                em {
                                    font-style: normal;
                                    color: #ff9100;
                                }
                            }
                        }
                        .radiobox {
                            padding: 0;
                            width: 24px;
                            text-align: center;
                        }
                    }
                    .datatable {
                        border-collapse: collapse;
                        margin: 0px;
                        border-width: 1px 0px;
                        .tr {
                            .td {
                                font-size: 13px;
                                line-height: 32px;
                                padding: 0px 10px;
                                border-width: 1px;
                                border-style: solid;
                                border-color: rgb(204, 207, 209);
                                border-image: initial;
                                &.tname {
                                    width: 90px;
                                    background-color: rgb(249, 250, 250);
                                    &.big {
                                        width: 120px;
                                    }
                                    &.bigger {
                                        width: 150px;
                                    }
                                }
                            }
                        }
                    }
                    .certimg {
                        width: 40px;
                        height: 30px;
                        vertical-align: middle;
                        cursor: pointer;
                        margin: 5px;
                    }
                    input.date {
                        // width: 210px;
                        background: url(../img/icons.png) right -213px no-repeat rgb(255, 255, 255);
                    }
                    .func {
                        font-size: 12px;
                        padding: 11px 0px 0px;
                        a {
                            display: inline-block;
                            line-height: 22px;
                            color: rgb(98, 98, 98);
                            padding: 0px 15px;
                            border-width: 1px;
                            border-style: solid;
                            border-color: rgb(165, 165, 165);
                            border-image: initial;
                            border-radius: 4px;
                            background: -webkit-gradient(linear, 0% 10%, 0 100%, from(rgb(255, 255, 255)), to(rgb(235, 235, 235)));
                            &:hover {
                                background-color: rgb(255, 255, 255);
                                color: rgb(255, 145, 0);
                                border-color: rgb(255, 145, 0);
                            }
                        }
                        .vehicle {
                            margin: 0 10px 0 0;
                            padding: 0 0 0 10px;
                            background: #fff;
                            border-color: #cccfd1;
                            i {
                                display: inline-block;
                                margin: 0 0 0 10px;
                                width: 16px;
                                height: 22px;
                                text-align: center;
                                background-color: #cccfd1;
                                color:#fff;
                                border-radius: 0 3px 3px 0;
                                font-style: normal;
                            }
                            &:hover {
                                border-color: #d9544f;
                                color: #4a4a4a;
                                i{background-color: #d9544f;}
                            }
                        }
                    }
                    .data-grid {
                        width: 100%;
                        .grid-title {
                            display: table;
                            border-collapse: collapse;
                            width: 100%;
                            table-layout: fixed;
                            background-color: rgb(249, 250, 250);
                            border-bottom: 1px solid rgb(204, 207, 209);
                            .tr{
                                border-top: 1px solid #cccfd1;
                                .th {
                                    display: table-cell;
                                    vertical-align: middle;
                                    text-align: left;
                                    font-weight: normal;
                                    padding: 10px 0px 10px 15px;
                                }
                            }
                        }
                    }
                    .bidlist {
                        font-size: 12px;
                        padding: 10px 0px;
                        .tr {
                            .td {
                                line-height: 40px;
                                color: rgb(153, 153, 166);
                                border-bottom: 1px dotted rgb(204, 207, 209);
                                border-top: none;
                                .blue{
                                    color: #337ab7;
                                }
                            }
                            &.thead {
                                .td {
                                    color: rgb(153, 153, 166);
                                    border-bottom: none;
                                    span {
                                        display: block;
                                        margin: 0px 10px 0px 0px;
                                        border-bottom: 4px solid rgb(225, 225, 225);
                                    }
                                    &.last {
                                        span{
                                            margin: 0px#337ab7;
                                        }
                                    }
                                }
                            }
                            &.success {
                                .td {
                                    color: rgb(217, 84, 79);
                                }
                            }
                            &.subbidlist {
                                display: none;
                               >.td {
                                padding: 20px;
                                border: none;
                               }
                            }
                        }
                        &.reportlist {
                            tbody {
                                .tr {
                                    .td {
                                        border-bottom: 1px solid rgb(204, 207, 209);
                                        .bidlist {
                                            .tr {
                                                &.thead {
                                                    .td {
                                                        border-bottom: none;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            
                        }
                    }
                    .platenumber {
                        padding-right: 15px;
                        background: url(../img/stamp.png) right center no-repeat;
                    }
                    .trackingbox {
                        margin: 0 auto;
                        .bidlist {
                            padding: 0;
                        }
                    }
                    .add-form{
                        input.input, select.select {
                            height: 22px;
                            line-height: 22px;
                        }
                        .table {
                            margin-bottom: 0;
                        }
                        dl{
                            display: table;
                            margin: 0 0 10px 0;
                            width: 100%;
                            height: 34px;
                            position: relative;
                            dt{
                                &.label{
                                    display: table-cell;
                                    width: 70px;
                                    margin: 0;
                                    padding: 0 10px 0 35px;
                                    text-align: right;
                                    vertical-align: middle;
                                }
                            }
                            dd{
                                &.inputs {
                                    display: table-cell;
                                    height: 34px;
                                    line-height: 34px;
                                    overflow: hidden;
                                    .td {
                                        display: table-cell;
                                        padding: 0 10px 0 0;
                                        .input {
                                            padding: 5px 0;
                                            width: 100%;
                                            text-indent: 5px;
                                        }
                                    }   
                                    label {
                                        font-weight: normal;
                                        input {
                                            display: inline-block;
                                            margin: 0 4px 0 0;
                                            vertical-align: middle;
                                        }
                                    } 
                                }
                                &.buttons {
                                    @extend .clearfix;
                                    padding-right: 15px;
                                    display: table-cell;
                                    width: 60px;
                                    height: 34px;
                                    overflow: hidden;
                                    vertical-align: middle;
                                    a {
                                        display: block;
                                        width: 28px;
                                        height: 28px;
                                        border: 1px solid #cacaca;
                                        border-radius: 8px;
                                        color: #adb0b4;
                                        background: url(../img/icons.png) no-repeat -544px 0px;
                                    }
                                    ul {
                                        margin: 2px 0;
                                        padding: 0;
                                        width: 60px;
                                        height: 30px;
                                        overflow: hidden;
                                        li {
                                            float: left;
                                            margin: 0 5px 0 0;
                                            &.btn-list {
                                                a {
                                                    background-position: -454px 0px;
                                                    &:hover {
                                                        border-color: #FF8D00;
                                                        background-color: #FF8D00;
                                                        background-position: -454px -25px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .filter {
                    border-top: 1px solid #cccfd1;
                    font-size: 13px;
                    .table{
                        .tr {
                            margin: 0;
                            .td {
                                padding: 4px 10px 5px 0;
                                background-color: #f9fafa;
                                border-bottom: 1px solid #cccfd1;
                                .input {
                                    width: 80%;
                                }
                                .select {
                                    height: 30px;
                                }
                            }
                            .tname {
                                width: 90px;
                                margin: 0;
                                text-align: right;
                                vertical-align: middle;
                            }
                        }
                    }
                    input.date {
                        background-position: right -215px;
                    }
                    .func {
                        text-align: center;
                    }
                }
                .pagebar {
                    padding: 0px 0px 0px 16px;
                }
            }
            .tracking , .lineoffer {
                .line{
                    margin: 0px 0px 20px;
                    border-width: initial;
                    border-style: none;
                    border-color: initial;
                    border-image: initial;
                    .title {
                        margin: 5px 0px 0px -20px;
                        height: 20px;
                        border-top: 1px solid rgb(242, 242, 242);
                        span {
                            height: 14px;
                            font-weight: 700;
                            line-height: 14px;
                            background-color: rgb(255, 145, 0);
                            color: rgb(255, 255, 255);
                            padding: 0px 10px;
                        }
                    }
                } 
                .bidlist {
                    padding: 5px 0px 0px;
                }
            }
            .lineoffer {
                .table {
                    .tr {
                        .td.linename {
                            font-weight: 700;
                            line-height: 16px;
                            padding: 5px 0px;
                            border-width: initial;
                            border-style: none;
                            border-color: initial;
                            border-image: initial;
                        }
                    }
                  &.lineprices {
                    margin: 5px 10px;
                        .tr{
                            .td {
                                font-size: 12px;
                                padding: 3px 0px;
                                border-top: none;
                            }
                        }
                        
                    }  
                }
            }
            .trackingbox {
                margin: 20px 0px;
            }
            .searchbar {
                margin: 0px 20px 0 20px;
                padding: 10px 0 0 0;
                height: 40px;
                border-bottom: 1px solid #d5d8da;
                font-size: 15px;
                line-height: 40px;
                color: #000;
            }
            .foot {
                text-align: center;
                margin: 25px 0px 0px;
                input.submit {
                    width: 150px;
                    height: 36px;
                    background-color: rgb(255, 145, 0);
                    color: rgb(255, 255, 255);
                    font-size: 14px;
                    cursor: pointer;
                    margin: 0px 30px 0px auto;
                    border-width: initial;
                    border-style: none;
                    border-color: initial;
                    border-image: initial;
                    border-radius: 4px;
                    outline: none;
                }
            }            
        }
    }  
}
.ui-dialog{
    .mt10 {
        margin-top: 10px;
    }
    .receipt{
        .imgcontainer {
            position: relative;
            width: 900px;
            height: 500px;
            overflow: hidden;
        }
        .funccontainer {
            width: 900px;
            height: 100px;
            >div{
                display: inline-block;
                width: 150px;
                float: left;
                text-align: center;
                position: relative;
                .imgsmall {
                    float: left;
                    margin-right: 12px;
                    margin-top: 5px;
                    width: 140px;
                    height: 90px;
                    &.on {
                        margin-right: 12px;
                        width: 130px;
                        height: 80px;
                        border: 5px solid #ff9100;
                    }
                }
            }
        }
        .imgprev, .imgnext {
            position: absolute;
            left: 3%;
            top: 50%;
            margin-top: -65px;
            display: block;
            width: 30px;
            height: 44px;
            background: url(../img/receipt.png) -15px -20px no-repeat;
        }
        .imgprev.on {
            background-position: -93px -20px;
        }
        .imgnext.on {
            background-position: -125px -20px;
            left: auto!important;
            right: 3%!important;
        }    
    }
    .listtable {
        max-height: 200px;
        overflow: auto;
        color: #9999a6;
        .table {
            border-spacing: 0;
            .radiobox {
                padding: 0;
                width: 24px;
                text-align: center;
            }
        }
        .tr {
            &:hover {
                background-color: #f9fafa;
            }
            .td {
                padding: 5px 10px;
                border-bottom: 1px solid #E3E6EB;
                line-height: 20px;
                color: #999;
                &.tname {
                    width: 90px;
                    background-color: #f9fafa
                }
            }
            &.thead {
                .td {
                    border: 1px solid #CBCFD2;
                    border-left: none;
                    line-height: 20px;
                    background-color: #fafafa;
                    color: #4a4a4a;
                }
                .last {
                    border-right: none;
                }
            }
        }
        .txt {
            line-height: 20px;
        }
        .input {
            padding: 3px 5px;
        }
        &.big {
            max-height: 600px;
        }
    }
    .edittable {
        .tr {
            .td {
                padding: 5px;
                vertical-align: middle;
                &.tname {
                    text-align: right;
                }
                border: none;
                input, select {
                    margin: 0 5px 0 0;
                    padding: 5px;
                    height: 22px;
                    line-height: 22px;
                    border: 1px solid #cacaca;
                    color: #343434;
                }
                label {
                    font-weight: normal;
                }
            }
        }
        .select {
            height: 32px;
            vertical-align: middle;
        }
    }
    .form {
        .table {
            margin: 10px 0;
            .tr {
                .td {
                    padding: 5px 0;
                }
            }
        }
    }
    .tabs {
        width: 310px;
        .title {
            margin: 0;
            padding: 0;
            border-bottom: 0px solid #d5d8da;
            text-align: center;
            font-size: 14px;
            color: #4a4a4a;
            .td {
                padding: 0;
                width: 150px;
                border-bottom: 1px solid #d5d8da;
                line-height: 39px;
                cursor: pointer;
            }
            .first {
                border-right: 1px solid #d5d8da;
            }
            .on {
                border-bottom: 1px solid transparent;
                line-height: 39px;
                background-color: #fff;
                color: #ff9100;
            }
        }
        .list {
            li {
                float: left;
                width: 50%;
                text-indent: 5px;
            }
        }
        &.large {
            width: 420px;
            .title {
                .td {
                    width: 240px;
                    font-size: 14px;
                    color: #4a4a4a;
                }
                .first {
                    width: 180px;
                }
                .left {
                    float: left;
                    margin-left: 20px;
                }
                .right {
                    float: right;
                    margin-right: 25px;
                }
            }
            .pnl {
                margin: 0;
                padding: 0;
                height: 100%;
                .td {
                    padding: 0;
                    width: 150px;
                    line-height: 39px;
                    cursor: pointer;
                    width: 240px;
                }
                .first {
                    border-right: 1px solid #d5d8da !important;
                    width: 180px !important;
                }
            }
            .list {
                height: 100%;
                max-height: 100%;
                li {
                    float: left;
                    width: 100%;
                    line-height: 38px;
                }
            }
        }
    }
    .table {
        &.bd {
            border-collapse: collapse;
            border: 1px solid #d5d8da !important;
            &.td {
                border-bottom: 1px dotted #d5d8da !important;
            }
            .thead {
                .td {
                    border-left: none !important;
                    border-right: none !important;
                    border-bottom: 1px solid #d5d8da !important;
                }
            }
            .last {
                border-right: 1px solid #d5d8da !important;
            }
            &.padtop {
                margin-top: 18px;
                width: 100%;
            }
            .last {
                border-right: 1px solid #d5d8da !important;
            }
        }
        &.bt {
            border-collapse: collapse;
            border: 1px solid #d5d8da !important;
            .td {
                border-bottom: 1px solid #d5d8da !important;
                border-right: 1px solid #d5d8da !important;
            }
        }
    }
    a.btn {
        display: inline-block;
        padding: 0 15px;
        line-height: 22px;
        border: 1px solid #a5a5a5;
        border-radius: 4px;
        color: #626262;
        background-color: #f9f9f9;
        background: -moz-linear-gradient(top, #ffffff, rgba(235, 235, 235, 1));
        background: -webkit-gradient(linear, 0% 10%, 0 bottom, from(#ffffff), to(rgba(235, 235, 235, 1)));
    }
    .funcbtn {
        /* position: relative; */
        _position: absolute;
        float: left;
        top: 0px;
        /* right: 25px; */
        _height: 20px;
        height: 20px;
        padding: 0 0px;
        font-size: 20px;
        font-weight: 400;
        line-height: inherit;
        color: #000;
        text-shadow: 0 1px 0 #FFF;
        opacity: .2;
        filter: alpha(opacity=20);
        cursor: pointer;
        background: transparent;
        _background: #FFF;
        border: 0;
        -webkit-appearance: none;
        &:hover,&:focus{
            color: #ff9100;
            text-decoration: none;
            cursor: pointer;
            outline: 0;
            opacity: 0.5;
            filter: alpha(opacity=50);
        }
        &.hbd {
            margin-right: 10px;
            border: 1px solid;
            height: 16px;
            width: 15px;
            border-radius: 5px;
            text-align: center;
            vertical-align: middle;
            line-height: 14px;
        }
    }
}

.ui-dialog-title{
    padding: 0 0 20px 0!important;
    &.subtitle {
        margin-top: 32px;
        margin-left: 0px;
        margin-right: 0px;
        color: #4a4a4a;
    }
}

/*弹窗信息搜索栏*/
.searcher {
    margin: 0px 0px;
    padding: 0px 0 0 0;
    color: #ddd;
    border: 1px solid #d5d8da;
    @extend .clearfix;
    position: relative;
    &:hover {
        border: 1px solid #ff9100;
    }
    input {
        float: left;
        margin: 0;
        height: 32px;
        line-height: 18px;
    }
    .input {
        width: 260px;
        border: none;
    }
    .btn {
        padding: 0;
        width: 40px;
        height: 32px;
        line-height: 32px;
        background: #fff url(../img/icons.png) no-repeat 10px -195px;
        color: #343434;
        border: none;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;
    }
}

.selectlist {
    margin: 20px 0 0 0;
    border: 1px solid #d5d8da;
    line-height: 36px;
    .title {
        padding: 0 20px;
        border-bottom: 1px solid #d5d8da;
        background-color: #f9fafa;
    }
    .list {
        max-height: 184px;
        overflow: auto;
        color: #9999a6;
        li {
            border-bottom: 1px dotted #d5d8da;
            margin-bottom: 0;
            padding: 0 5px;
            .radio, .checkbox{
                display: inline-block;
                margin: auto;
            }
            label {
                font-weight: normal;
                color: #000;
            }
        }
    }
    .table {
        display: table;
        width: 100%;
        .tr {
            display: table-row;
            .td {
                display: table-cell;
                padding: 0;
                vertical-align: middle;
            } 
        }
    }
}

.recommend {
    width: 100%;
    margin: auto;
    height: 616px;
    padding: 24px 0 0 0;
    background-color:#fff; 
    .panel {
        width: 1250px;
        margin: auto;
        .head {
            padding: 0 20px 15px 0;
            height: 14px;
            font-size: 15px;
            h3{
                font-weight: normal
            }
        }
        .listtable {
            table{
                border-collapse: collapse;
                .thead .td {
                    border: none;
                    border-top: 5px solid #ff9100;
                    border-bottom: 1px solid #d4d8db;
                    line-height: 21px;
                    background-color: #f2f2f2;
                    color: #343434;
                    font-size: 14px;
                }
                tr {
                    td{
                        padding: 4px 0;
                        border-bottom: 1px solid #d4d8db;
                        vertical-align: middle;
                        font-size: 13px;
                        color: #343434;
                        &:first-child {
                            padding-left: 16px;
                            border-left: 1px solid #fff;
                        }
                        .btn {
                            color: initial;
                            padding: 5px 8px;
                            font-size: 12px;
                            background-color: #fafafa;
                            outline: none;
                            border: 1px solid #c1c8d2;
                            cursor: pointer;
                            &:hover {
                                background-color: #ff9100;
                                border: 1px solid #ff9100;
                                color: #fff;
                            }
                        }
                    }
                }
                
            }
        }
    }
}
