@charset 'utf-8';

footer {
    margin: 90px 0 0 0;
    @include size(100%, 199px);
    background: #343434 url();
    color: #999;
    .links {
      @extend .car-wrap;
      color: #ccc;
      .info {
        @extend .pull-left;
        margin: 82px 0 0 95px;
        font-size: 13px;
        line-height: 13px;
        a {
          color: #ccc;
        }
        .split {
          display: inline-block;
          margin: 0 30px;
          height: 10px;
          border-left: 1px solid #666;
          font-size: 4px;
          line-height: 4px;
          overflow: hidden;
        }
        .copyrights {
          padding: 12px 0 0 0;
          font-size: 12px;
          line-height: 12px;
          color: #999;
          a + a {
            margin-left: 40px;
            img {
              width: 20px;
              margin-top: -3px;
              margin-right: 3px;
            }
          }
        }
      }
      .qrcode {
        @extend .pull-right;
        margin: 55px 80px;
        .img {
          @extend .pull-left;
          @include size(89px);
          background: url(../img/wxqrcode.jpg) no-repeat;
          background-size: contain;
        }
        .txt {
          @extend .pull-right;
          margin: 16px 0 0 28px;
          font-size: 13px;
          line-height: 24px;
          color: #ccc;
        }
      }
    }
}
