@charset 'utf-8';


$font-id: '373284_9mwsynlxgrl1h5mi';
@font-face {
  font-family: 'iconfont';  /* project id 373284 */
  src: url('//at.alicdn.com/t/font_#{$font-id}.eot');
  src: url('//at.alicdn.com/t/font_#{$font-id}.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_#{$font-id}.woff') format('woff'),
  url('//at.alicdn.com/t/font_#{$font-id}.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_#{$font-id}.svg#iconfont') format('svg');
}
.iconfont {
  @include iconfont;
  font-size: 13px;
  &.icon-left {
      margin-right: 5px;
  }
  &.icon-right {
      margin-left: 5px;
  }
}
$iconfonts: (
  home:       '\e64a',
  member:     '\e613',
  arrowdown:  '\e601',
  logout:     '\e6dd',
  search:     '\e614',
  tridown:    '\e609',
  moneyrmb: '\e663',
  moneydollar: '\e628',
  why: '\e75d',
  phone: '\e66c',
  checked: '\e625',
  photo : '\e71e',
  smiliface : '\e60d',
  true : '\e604',
  lightning : '\e602',
  colock :'\e619',
  close: '\e61a',
  sort: '\e650',
  asc:'\e611',
  desc:'\e615',
  cart:'\e600',
  gou: '\e65c',
  step1: '\e659',
  step2: '\e67d',
  step3: '\e603',
  step4: '\e616',
);

@each $icon, $value in $iconfonts {
  .icon-#{$icon}:before {
    content: '#{$value}';
  }
}
