/*省市区 未改造*/

.city-picker-dropdown {
    position: absolute;
    width: 315px;
    left: -9999px;
    top: -9999px;
    outline: 0;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    z-index: 99;
    display: none;
    min-width: 330px;
    margin-bottom: 20px;
    &.has-arrow:before {
        content: "▲";
        position: absolute;
        top: 0;
        left: 0;
        height: 15px;
        color: #ff9100;
    }
    .city-picker-input {
        opacity: 0 !important;
        top: -9999px;
        left: -9999px;
        position: absolute;
    }
    .city-input-clear {
        position: absolute;
        top: 50%;
        margin-top: -12.5px;
        margin-left: -12.5px;
        display: none;
        width: 25px;
        height: 25px;
        background: url(images/home/icons.png) no-repeat right 0px;
    }
    .city-picker-span {
        position: relative;
        display: block;
        outline: 0;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        border: 1px solid #c1c8d2;
        background-color: #fff;
        color: #4a4a4a;
        cursor: pointer;
        overflow: hidden;
    }
    .city-picker-span>.placeholder {
        color: #aaa;
        font-size: 13px;
        font-family: 'Microsoft YaHei', Arial, Verdana, Sans-serif;
    }
    .city-picker-span>.arrow {
        position: absolute;
        top: 50%;
        right: 8px;
        width: 10px;
        margin-top: -3px;
        height: 5px;
        background: url(../images/drop-arrow.png) -10px -25px no-repeat;
    }
    .city-picker-span.focus,
    .city-picker-span.open {
        /*  border-bottom-color: #46A4FF;*/
    }
    .city-picker-span.open>.arrow {
        background-position: -10px -10px;
    }
    .city-picker-span>.ctitle>span {
        color: #333;
        padding: 5px;
        border-radius: 3px;
    }
    .city-picker-span>.ctitle>span:hover {
        background-color: #ffa800;
        color: #fff;
    }
    .city-select-wrap {
        box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
    }
    .city-select-tab {
        border-bottom: 1px solid #ccc;
        background: #f0f0f0;
        font-size: 13px;
    }
    .city-select-tab>a {
        display: inline-block;
        padding: 8px 15px;
        border-left: 1px solid #ccc;
        border-bottom: 1px solid transparent;
        color: #4D4D4D;
        text-align: center;
        outline: 0;
        text-decoration: none;
        cursor: pointer;
        font-size: 14px;
        margin-bottom: -1px;
    }
    .city-select-tab>a.active {
        background: #fff;
        border-bottom: 1px solid #fff;
        color: #ff9100 !important;
    }
    .city-select-tab>a:first-child {
        border-left: none;
    }
    .city-select-tab>a:last-child.active {
        border-right: 1px solid #ccc;
    }
    .city-select-content {
        /* width: 100%;*/
        min-height: 10px;
        background-color: #fff;
        padding: 10px 15px;
        max-height: 455px;
        overflow-y: scroll;
        position: relative;
    }
    .city-select {
        font-size: 13px;
    }
    .city-select dl {
        line-height: 2;
        clear: both;
        padding: 3px 0;
        margin: 0!important;
        border-bottom: 1px dotted #ccc;
        height: auto!important;
    }
    .city-select dl h4 {
        padding: 0 !important;
        font-weight: bold !important;
        font-size: 14px !important;
        line-height: 2;
    }
    .city-select dl:last-child {
        border-bottom: none;
    }
    .city-select dt {
        position: absolute;
        width: 2.5em;
        font-weight: 500;
        text-align: left !important;
        line-height: 2;
    }
    .city-select dt a.active {
        display: inline-block;
        border-radius: 4px;
    }
    .city-select dt i {
        display: inline-block;
        height: 26px;
        text-align: center;
        color: #fff;
        font-style: normal;
        float: right;
        font-size: large;
        line-height: 26px;
        margin-left: 10px;
    }
    .city-select dd {
        margin-left: 0;
        line-height: 2;
        float: left !important;
        text-align: left;
    }
    .city-select.province dd {
        margin-left: 3em;
    }
    .city-select a {
        display: inline-block;
        padding: 0 10px;
        outline: 0;
        text-decoration: none;
        white-space: nowrap;
        margin-right: 2px;
        text-decoration: none;
        color: #333;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .city-select a:hover,
    .city-select a:focus {
        background-color: #ffa800 !important;
        border-radius: 2px !important;
        color: #fff !important;
    }
    .city-select a.active {
        background-color: #ff9100 !important;
        color: #fff !important;
        border-radius: 2px !important;
    }
    .city-picker-span.error {
        border: solid 1px #FF0000;
        animation: mymove 0.2s 5 ease;
        -webkit-animation: mymove 0.2s 5 ease;
        /* Safari and Chrome */
    }
    .clearfix:after {
        content: "";
        display: table;
        clear: both;
    }
    @keyframes mymove {
        0% {
            border: solid 1px #cacaca;
        }
        100% {
            border: solid 1px #FF0000;
        }
    }
    @-webkit-keyframes mymove/* Safari and Chrome */
    {
        0% {
            border: solid 1px #cacaca;
        }
        100% {
            border: solid 1px #FF0000;
        }
    }
}

/**loading*/
.ajaxload .loader:before,.ajaxload .loader:after,.ajaxload .loader {
    border-radius: 50%;
    width: 1.2em;
    height: 1.2em;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation: ajaxload 1.8s infinite ease-in-out;
    animation: ajaxload 1.8s infinite ease-in-out;
}
.ajaxload .loader {
    margin: 0.25em auto;
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}
.ajaxload .loader:before {
    left: -3em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}
.ajaxload .loader:after {
    left: 3em;
}
.ajaxload .loader:before,.loader:after {
    content: '';
    position: absolute;
    top: 0;
}
@-webkit-keyframes ajaxload {
    0%,
    80%,
    100% {
        box-shadow: 0 2.5em 0 -1.3em #ff9100;
    }
    40% {
        box-shadow: 0 2.5em 0 0 #ff9100;
    }
}
@keyframes ajaxload {
    0%,
    80%,
    100% {
        box-shadow: 0 2.5em 0 -1.3em #ff9100;
    }
    40% {
        box-shadow: 0 2.5em 0 0 #ff9100;
    }
}
//翻页
#pageNav,.pageNav {
  margin: 60px auto;
  .pagebar {
    display: flex;
  }
  .page-item {
    .page-link {
      border-color: $border-color;
      color: $font-normal;
      border-radius: 0.25em;
      padding: 0.5rem 0.9rem;
      height: 40px;
      line-height: 1.5;
    }
    &.active {
      .page-link {
        color: #fff;
        background-color: $font-yellow;
        border-color: $font-yellow;
      }
    }
    + .page-item {
      margin-left: 10px;
    }
    &.disabled {
      .page-link {
        color: #d0cfd8;
        background-color: #f5f4fa;
      }
    }
  }
  .page-goto {
    display: inline-block;
    margin-left: 35px;
    span {
      display: inline-block;
      height: 40px;
      line-height: 40px;
      color: $font-dark; 
      &:nth-of-type(1) {
        margin-right: 18px;
      }     
    }
    input {
      @include size(40px);
      border: 1px solid $border-color;
      margin: 0 8px;
      text-align: center;
    }
    a {
      display: inline-block;
      line-height: 40px;
      height: 40px;
      border: 1px solid $border-color;
      border-radius: 0.25rem;
      padding: 0 20px;
      margin-left: 16px;
      color: inherit;
      text-decoration: none;
      a:visited {
        text-decoration: none;
      }
    }
  }
}

