/**loading*/
.ajaxload .loader:before, .ajaxload .loader:after, .ajaxload .loader {
  border-radius: 50%;
  width: 1.2em;
  height: 1.2em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: ajaxload 1.8s infinite ease-in-out;
  animation: ajaxload 1.8s infinite ease-in-out;
}

.ajaxload .loader {
  margin: 0.25em auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.ajaxload .loader:before {
  left: -3em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.ajaxload .loader:after {
  left: 3em;
}

.ajaxload .loader:before, .loader:after {
  content: '';
  position: absolute;
  top: 0;
}

@-webkit-keyframes ajaxload {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em #ff9100;
  }
  40% {
    box-shadow: 0 2.5em 0 0 #ff9100;
  }
}

@keyframes ajaxload {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em #ff9100;
  }
  40% {
    box-shadow: 0 2.5em 0 0 #ff9100;
  }
}

