  background: rgba(0, 0, 0, 0.3);
}

#gallery .gallery .image {
  position: absolute;
  margin: 0;
  /*之所以不使用auto直接垂直居中，是因为当图片旋转时left值会很难计算*/
  padding: 0;
  z-index: 999;
  display: none;
}

#gallery .gallery .image.active {
  display: block;
}

#gallery .gallery .tool {
  position: absolute;
  bottom: 4px;
  width: 100%;
  text-align: center;
  display: none;
  z-index: 99999;
}

#gallery .gallery .toolct {
  display: inline-block;
  height: 30px;
  background-color: #6f6965;
  padding: 5px 14px;
  box-sizing: border-box;
  border-radius: 6px;
}

#gallery .gallery .toolct span {
  margin-right: 20px;
}

#gallery .gallery .toolct i {
  display: inline-block;
  cursor: pointer;
}

#gallery .gallery .percentTip {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 100px;
  height: 30px;
  z-index: 9999;
  text-align: center;
  line-height: 30px;
  font-size: 16px;
  border-radius: 8px;
  color: #ffffff;
  filter: progid:DXImageTransform.Microsoft.Gradient(startColorStr='#4F504E', endColorStr='#151313', gradientType='1');
  background: linear-gradient(315deg, #4F504E, #151313);
  background: -moz-linear-gradient(315deg, #4F504E, #151313);
  background: -o-linear-gradient(left, #4F504E, #151313);
  background: -webkit-gradient(linear, 100% 0%, 100% 0%, from(#4F504E), to(#151313));
}

#gallery .gallery .thumbnails {
  position: absolute;
  right: 1px;
  bottom: 1px;
  border: 1px solid #ffffff;
  z-index: 99999;
  border-radius: 5px;
  background-color: rgba(177, 177, 177, 0.5);
  display: none;
}

#gallery .gallery .thumbnails .thumbDrag {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100px;
  height: 100px;
  border: 1px solid black;
  box-sizing: border-box;
}

#gallery .gallery .thumbnails .thumbDrag span {
  width: 100%;
  height: 100%;
  border: 1px solid white;
  display: block;
  box-sizing: border-box;
}

#gallery .gallery .thumbnails .thumbClose {
  position: absolute;
  right: 1px;
  top: -2px;
  cursor: pointer;
  height: 13px;
}

#gallery .gallery .thumbnails img {
  max-width: 100%;
  max-height: 100%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

#gallery .gallery .oper {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  height: 38px;
  z-index: 99999;
}

#gallery .gallery .oper i {
  display: none;
  cursor: pointer;
}

#gallery .gallery .oper span {
  width: 22px;
  height: 38px;
}

#gallery .gallery .oper .prev {
  float: left;
  margin-left: 9px;
}

#gallery .gallery .oper .next {
  float: right;
  margin-right: 9px;
}

#gallery .gallery .oper .prev.active i,
#gallery .gallery .oper .next.active i {
  display: inline-block;
}

/*分页样式*/
.pagebar {
  padding: 0 0 0 16px;
}

.pagebar .func {
  float: left;
  margin-left: 20px;
}

.pagebar .func .checkbox {
  margin: 0 5px 0 0;
}

.pagebar .split {
  margin: 0 10px;
  color: #ccc;
}

.pagebar .pagination {
  float: right;
  margin-top: 0;
}

#carCarousel {
  width: 100%;
  height: 560px;
  background-color: #fff;
  overflow: hidden;
}

#carCarousel .item {
  height: 560px;
}

#carCarousel .item:nth-child(1) {
  background: url(../img/1.png) center center no-repeat;
}

#carCarousel .item img {
  margin: auto;
  width: 100%;
  height: 100%;
}

.service {
  width: 100%;
  height: 335px;
  padding: 106px 0 120px 0;
  border-bottom: 1px solid #e8e8ed;
}

.service .car-wrap, .service header .fast, header .service .fast, .service header .nav, header .service .nav, .service footer .links, footer .service .links {
  height: 100%;
}

.service ul {
  display: table;
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
  height: 100%;
}

.service ul li {
  display: table-cell;
  width: 100%;
  position: relative;
  text-align: center;
}

.service ul li:before {
  content: '';
  top: 0;
  left: 50%;
  position: absolute;
  margin-left: -38px;
  width: 76px;
  height: 76px;
  background: #fff no-repeat center center;
}

.service ul li span {
  display: inline-block;
  margin-top: 95px;
}

.service ul li:nth-child(1):before {
  background-image: url("../img/z.png");
}

.service ul li:nth-child(2):before {
  background-image: url("../img/d.png");
}

.service ul li:nth-child(3):before {
  background-image: url("../img/k.png");
}

.service ul li:nth-child(4):before {
  background-image: url("../img/a.png");
}

.palte-container {
  width: 100%;
  margin-top: 50px;
}

.plate {
  width: 50%;
  height: 400px;
  float: left;
  position: relative;
}

.plate.market {
  background: url(../img/carmarket.png) no-repeat center center;
}

.plate.market a:before {
  content: '';
  background: url(../img/carcart.png) no-repeat center center;
  width: 118px;
  height: 99px;
  top: 114px;
  left: 267px;
  position: absolute;
}

.plate.insurance {
  background: url(../img/insumarket.png) no-repeat center center;
}

.plate.insurance a:before {
  content: '';
  background: url(../img/inscart.png) no-repeat center center;
  width: 93px;
  height: 102px;
  top: 114px;
  left: 267px;
  position: absolute;
}

.plate:before {
  content: '';
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  position: absolute;
  background-color: #000;
  opacity: 0.3;
  transition: 0.2s;
}

.plate:hover:before {
  opacity: 0;
}

.plate:hover a {
  color: #ff9100;
}

.plate:hover a:after {
  border-bottom-color: #ff9100;
}

.plate:hover.market a:before {
  background: url(../img/carcarty.png) no-repeat center center;
}

.plate:hover.insurance a:before {
  background: url(../img/inscarty.png) no-repeat center center;
}

.plate a {
  top: 0px;
  left: 0px;
  position: absolute;
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: block;
  transition: 0.2s;
  color: #fff;
  font-size: 20px;
  text-align: center;
  padding-top: 290px;
}

.plate a:after {
  content: '';
  left: 305px;
  position: absolute;
  width: 0;
  height: 0;
  border-bottom: 8px solid #fff;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 0;
}

.plate a p + p {
  font-size: 14px;
  margin-top: 10px;
}

.partner {
  margin-top: 35px;
  text-align: center;
}

.partner h3 {
  font-size: 20px;
  color: #343434;
}

.partner .partners {
  margin-top: 45px;
  display: table;
  border-collapse: collapse;
  width: 100%;
  table-layout: fixed;
}

.partner .partners > li {
  display: table-cell;
  width: 100%;
  height: 120px;
  text-align: left;
}

.partner .partners > li:last-child {
  width: 190px;
}

.partner .partners > li a {
  display: inline-block;
  border: 1px solid #e8e8ed;
  width: 190px;
  height: 120px;
  text-align: center;
}

.partner .partners > li a img {
  width: 100%;
  height: 100%;
}

.qrskin .ui-dialog-title {
  display: none;
}
