.palte-container {
  width: 100%;
  margin-top: 50px;
}
.plate {
  @include size(50%, 400px);
  float: left;
  @include relative();
  &.market {
    background: url(../img/carmarket.png) no-repeat center center;
    a:before {
      content: '';
      background: url(../img/carcart.png) no-repeat center center;
      @include size(118px, 99px);
      @include absolute(top 114px left 267px);
    }
  }
  &.insurance {
    background: url(../img/insumarket.png) no-repeat center center;
    a:before {
      content: '';
      background: url(../img/inscart.png) no-repeat center center;
      @include size(93px, 102px);
      @include absolute(top 114px left 267px);
    }
  }
  &:before {
    content: '';
    @include size(100%);
    @include absolute(top 0px left 0px);
    background-color: #000;
    opacity: 0.3;
    transition: 0.2s;
  }
  &:hover {
    &:before {
      opacity: 0;
    }
    a {
      color: $font-yellow;
      &:after {
        // transform: rotate(180deg);
        // transform-origin: 50% 50% 0;
        border-bottom-color: $font-yellow;
      }
    }
    &.market {
      a:before {
        background: url(../img/carcarty.png) no-repeat center center;
      }
    }
    &.insurance {
      a:before {
        background: url(../img/inscarty.png) no-repeat center center;
      }
    }
  }
  a {
    @include absolute(top 0px left 0px);
    @include size(100%);
    text-decoration: none;
    display: block;
    transition: 0.2s;
    color: #fff;
    font-size: 20px;
    text-align: center;
    padding-top: 290px;
    &:after {
      content:'';
      @include absolute(left 305px);
      @include size(0);
      border: {
        bottom: 8px solid #fff;
        left: 8px solid transparent;
        right: 8px solid transparent;
        top: 0;
      }
    }
    p + p {
      font-size: 14px;
      margin-top: 10px;
    }
  }
}