body {
  color: #000000;
  background: #f2f4f5;
  font-size: 14px;
  font-family: "Microsoft YaHei", 微软雅黑, 宋体, Helvetica, Arial , sans-serif;
  @include size(100%);
}
.car-wrap {
  @extend .clearfix;
  margin: auto;
  width: 1250px;
}
body, ul, li, h1, h2, h3, h4, p, dl, dt, dd {
  margin: 0;
  padding: 0;
  list-style: none;
}
button, input, optgroup, select, textarea {
  outline: none;
  &:focus {
    outline: none;
  }
}
a {
  color: $font-link;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
  outline: 0;
}
