@charset 'utf-8';

header {
  @include size(100%,110px);
  .fixed-header {
    @include size(100%,110px);
    position: fixed;
    z-index: 19;
    background-color: #fff;
  }
  .fast-container {
    @include size(100%, 30px);
    background-color: #000;
  }
  .fast, .nav {
    @extend .car-wrap;
  }
  .fast {
    > .fast-nav {
      @extend .pull-left;
      list-style: none;
      > li {
        @extend .pull-left;
        height: 30px;
        padding: 0 13px;
        &:first-child {
          padding-left: 0;
        }
        + li {
          border-left: 1px solid #2a2a2a;
          border-right: 1px solid #434343;
        }
        &:last-child {
          border-right: none;
        }
        > a {
          display: inline-block;
          height: 30px;
          line-height: 30px;
          text-align: center;
          vertical-align: middle;
          font-size: 12px;
          color: #fff;
          &:hover {
            color: $font-yellow;
            text-decoration: none;
          }
        }
      }
    }
    > .fast-login {
      @extend .pull-right;
      z-index: 99;
      li {
        @extend .pull-left;
        a {
          display: inline-block;
          height: 30px;
          line-height: 30px;
          text-align: center;
          vertical-align: middle;
          font-size: 12px;
          color: #fff;
          &:hover {
            color: $font-yellow;
            text-decoration: none;
          }
        }
        .member {
          @extend .pull-right;
          .items {
            @include relative;
            @extend .pull-left;
            margin: 0;
            > li {
              > a {
                display: block;
                width: 90px;
                line-height: 30px;
                text-align: center;
              }
              
              &.group {
                @include relative;
                > a {
                  padding: 0;
                  width: auto;
                  .icon-arrowdown:before {
                    transition: transform .2s ease-out 0s;
                    display: inline-block;
                  }
                  &:hover {
                    text-decoration: none;
                  }
                }
                > .menu {
                  display: none;
                  padding: 8px;
                  position: absolute;
                  left: -20px;
                  background: rgba(255,255,255,1);
                  border-top: none;
                  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
                  border-radius: 0 0 4px 4px;
                  z-index: 99999;
                  > li > a {
                    display: block;
                    padding: 0 0 0 30px!important;
                    width: 144px;
                    border-radius: 0;
                    text-align: left;
                    line-height: 30px;
                    color: $font-normal;
                    &:hover {
                      background-color: #f2f2f2;
                    }
                    .ico-logout {
                      @extend .pull-right;
                      @include size(28px,30px);
                    }
                  }
                }
                &:hover {
                  > .menu {
                    display: block;
                  }
                  .icon-arrowdown::before {
                    transform: rotate(180deg);
                    transform-origin: 50% 50% 0px;
                  }
                }
              }
              &.nickname {
                @extend .pull-left;
                margin: 0;
                > a {
                  padding: 0;
                  width: auto;
                  color: #fff;
                  &:hover {
                    color: $font-yellow;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
        .hotline {
          line-height: 30px;
          font-size: 12px;
          color: #fff;
        }
      }
      .tsplit {
          width: 40px;
          height: 30px;
          span {
            margin-top: 8px;
            margin-left: 20px;
            border-left: 1px solid $border-color;
            line-height: 13px;
            display: inline-block;
            height: 13px;
            text-align: center;
            vertical-align: middle;
            font-size: 12px;
          }
        }
    }
   }
  .nav-container {
    @include size(100%, 80px);
    box-shadow: 0px 2px 2px rgba(0,0,0,0.1);
  }
  .nav {
    display: table;
    @include size(1250px,80px);
    border-collapse: collapse;
    table-layout: fixed;
    > div {
      display: table-cell;
      width: 100%;
    }
    #logo {
      width: 220px;
      text-indent: 0;
      h1 {
        font-size: 18px;
        height: 80px;
        > a {
          display: inline-block;
          > div {
            display: inline-block;
            @include size(72px,80px);
            color: $font-yellow;
            vertical-align: middle;
            margin-left: 5px;
            font-weight: normal;
            .en-name {
              font-size: 12px;
            }
          }
        }
      }
    }
    #search {
      width: 160px;
      position: relative;
      input {
        @include size(160px, 35px);
        border: 1px solid $border-color;
        border-radius: 16px;
        text-indent: 18px;
        padding: 10px 0;
        font-size: 12px;
        &:focus {
          border-color: darken($border-color , 10%);
        }
      }
      a {
        position: absolute;
        right: 18px;
        top: 0;
        height: 80px;
        line-height: 80px;
        i {
          font-size: 16px;
          color: $font-normal;
        }
      }
    }
    #cart {
      width: 160px;
      position: relative;
      cursor: pointer;
      input {
        @include size(160px, 35px);
        border: 1px solid $border-color;
        background-color: #fff;
        text-indent: 18px;
        font-size: 12px;
        color: $font-number;
        cursor: pointer;
      }
      i {
          font-size: 16px;
          color: $font-number;
          position: absolute;
          left: 30px;
          top: 30px;
        }
      #cartNum {
        font-weight: normal;
        position: absolute;
        font-size: 10px;
        background-color: $font-number;
        color: #fff;
        width: 18px;
        height: 18px;
        border-radius: 9px;
        text-align: center;
        line-height: 18px;
        right: 22px;
        top: 25px;
      }
    }
    #navLinks {
      padding: 0 100px;
      vertical-align: middle;
      .nav-links {
        display: table;
        @include size(100%,100%);
        table-layout: fixed;
        a {
          display: table-cell;
          width: 100%;
          color: $font-normal;
          font-size: 16px;
          text-align: center;
          vertical-align: middle;
          &:hover {
            color: $font-yellow;
          }
          position: relative;
          overflow: hidden;
          &.current:after {
            content: '';
            @include size(0);
            border: {
              top: 0;
              right: 8px solid transparent;
              bottom: 10px solid $font-yellow;
              left: 8px solid transparent;
            }
            position: absolute;
            left: 50%;
            margin-left: -8px;
            bottom: -3px;
          }
        }
      }
    }
  }
}
.sign-content {
  padding: 5px 72px 45px 72px;
  form {
    width: 430px;
    margin: auto;
    .form-group {
      position: relative;
      margin-bottom: 11px;
      .form-control {
        height: 34px;
        font-size: 13px;
        &.btn-primary {
          height: 38px;
          border-radius: 5px;
          background-color: #009ef5 !important;
          border-color: #009ef5!important;

          &:focus,&:hover {
            background-color: #008ad8!important;
            border-color: #008ad8!important;
          }
        }
      }
      a.small {
        display: inline-block;
        position: absolute;
        right: 0;
        margin-top: 8px;
        font-size: 12px;//a.normal
        color: #009ef5;
      }
      &.chk-rem {
        .form-check-label {
          font-size: 12px;
          .form-check-input {
            top: 1px;
            margin-top: 0.3em;
          }
        }
        a.small {
          margin-top: 0.3rem;
        }
      }
    }
  }
  .for-sign {
    text-align: center;
    padding-top: 30px;
    font-size: 12px;
    p {
      width: 100%;
    }
    a {
      margin-left: 5px;
      color: #009ef5;
      &:hover {
        color: #008ad8;
      }
    }
  }
}
.login-dialog {
  .ui-dialog-header{
    border-bottom: none;
    .ui-dialog-title {
      color: $font-normal;
      text-align: center;
      font-weight: normal;
      font-size: 16px;
      margin-top: 25px;
    }  
  }
  .ui-dialog-footer {
    display: none;
  }
}

